mi nito sa bawat 16-litro sprayer na nakasaad sa label. Para sa mga produktong MAY Roundup® Ready Corn 2 Trait: FIRST SPRAYING. Mag-spray ng rekomendadong glyphosate herbicide (aprubado ng Fertilizer & Pesticide Authority o FPA) mula 12-15 DAP o kung ang damo ay may 1-2 pulgada ang taas. Sundin ang tamang dami ng herbicide sa bawat 16-litro na sprayer na nakasaad sa label. Hindi na kailangan ng shallow cultivation at pagsungkal (off-baring) dito. SECOND SPRAYING (OPSYONAL LAMANG). Mag-spray muli sa 35-40 DAP o kung ang damo ay may 1-2 pulgada ang taas. ")])]),_c('div',{staticClass:"mt-4"},[_c('span',{staticClass:"primary white--text headline pa-3",attrs:{"fab":""}},[_vm._v(" 7. ")]),_c('span',{staticClass:"title ml-2"},[_vm._v("PAGPUKSA SA PESTE")])]),_c('br'),_c('div',[_c('span',[_vm._v(" Mahalaga: Basahin ang mga nakasaad sa lalagyan ng produkto at siguruhing protektado ka sa inyong pananamit bago mag-spray. Para sa Conventional na produkto na WALANG YieldGard® Corn Borer Protection Trait: OPTION #1: Gumamit ng granular insecticide tulad ng Carbofuran o Fipronil sa basal na aplikasyon para sa mga peste tulad ng langgam, white grub, corn thrip, cutworm, at seedling maggot. OPTION #2: Kapag napapansin ang tama ng cutworm, earworm, o brown planthopper/wayawaya, maghalo ng 2-3 scoops Lannate™ 40SP sa 16-litro sprayer at ilagay mula 7-30 araw pagkatanim. OPTION #3: Sa pagkontrol ng locusts at planthopper/waya-waya, maglagay ng xylene-based insecticide na Cypermethrin mula 48-65 pagkatanim. Kung may nabibiling golden natural sprayable oil (GNSO), maghalo ng 40 ml. nito sa bawat 16 litro ng tubig ng sprayable insecticide upang maging mas mabisa ang pagpuksa, lalo na sa brown planthopper/waya-waya. Para sa produkto na MAY YieldGard® Corn Borer Borer Protection Trait: OPTION #1: Magtanim ng Pioneer® hybrid na may YIELDGARD® CORN BORER PROTECTION TRAIT para sa proteksyon laban sa corn borer, true armyworm, at pink stemborer (Sesamia). OPTION #2: Gumamit ng granular insectic